<template>
    <div class="box">
        <!-- <div class="choiceBox">
            <el-select v-model="workYears" placeholder="工作年数" class="choice">
                <el-option v-for="item in workYearsC" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="eduBackground" placeholder="教育背景" class="choice">
                <el-option v-for="item in eduBackgroundC" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="age" placeholder="年龄" class="choice">
                <el-option v-for="item in ageC" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
        </div> -->
        <div class="resumeBox">
            <el-empty description="暂无收藏" v-if="!collectList.length" style="transform: translateY(200px);"></el-empty>
            <div class="resume" :style="{ display: hidden }" v-for="i in collectList" :key="i.id">
            <div class="name">{{ i.name }}</div>
            <div class="age"><span>年龄：{{ i.age }}</span> <span>工作年限：{{ i.workingYears }}</span> <span>学历：{{ i.education
            }}</span></div>
            <div class="want"><span>求职期望：{{ i.career }} </span></div>
            <div class="honor"><span>荣誉证书：{{ i.honor }}</span></div>
            <div class="ex">主要经历：{{ i.workExperience }}</div>
            <el-rate :value="score(i.total)" disabled text-color="#ff9900" class="score">
            </el-rate>
            <el-button type="primary" class="details" size="mini" @click="goDetail(i.id)">查看详情</el-button>
            <el-button type="info" class="cancel" size="mini" @click="hide(i.id)">取消收藏</el-button>
        </div>
        </div>

        <div class="pages" v-if="this.total">
            <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize"
                class="pagesContainer" @current-change="pageChange">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import { reqCollectResume,reqUnCollect } from '@/api/index'
export default {
    name: 'upload',
    data() {
        return {
            collectList: [],
            hidden: 'block',
            area: '',
            total:0,
            pageSize:4
        };
    },
    methods: {
        //页数改变
        async pageChange(e){
            const res = await reqCollectResume(localStorage.getItem('id'),e)
            this.collectList = res.data
        },
        //前往详情
        goDetail(Id) {
            this.$router.push({ name: 'Analyze', params: { id: Id } })
        },
        //取消收藏
        async hide(id) {
            const res = await reqUnCollect(id)
            if(res.code==1){
                this.$message('取消收藏成功')
                this.getCollectResume()
            }
        },
        //获取收藏列表
        async getCollectResume() {
            const res = await reqCollectResume(localStorage.getItem('id'),0)
            if (res.code == 1) {

                this.total = res.data.length
                if (this.total > 4) {
                    this.collectList = res.data.slice(0, 4)
                } else {
                    this.collectList = res.data.slice(0, this.total)
                }
            }
        }
    },
    mounted() {
        this.getCollectResume()
    },
    computed:{
        score() {
            return function (e) {
                return Math.floor(e / 5)
            }
        }
    }
}
</script>
<style lang="scss" scoped>
::v-deep .el-input__inner {
    border-color: black;
    color: black;

    &::placeholder {
        color: grey;
    }
}

.box {
    width: 80%;
    float: left;
    margin-left: 20px;
    margin-top: 50px;
    margin-bottom: 100px;

    .pages {
        width: 100%;
        height: 100px;
        position: relative;

        .pagesContainer {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .resumeBox {
        width: 100%;
        height: 700px;
    }

    .resume {
        width: 100%;
        border: 1px solid black;
        height: 150px;
        margin-top: -20px;
        margin-bottom: 30px;
        border-radius: 10px;
        position: relative;

        .name {
            position: absolute;
            top: 15%;
            left: 8%;
            font-weight: 700;
            font-size: 15px;
        }

        .age {
            position: absolute;
            top: 35%;
            left: 90px;
            width: 250px;

            span {
                margin-left: 10px;
            }
        }

        .want {
            position: absolute;
            top: 55%;
            width: 250px;
            left: 90px;

            span {
                margin-left: 10px;
            }
        }

        .honor {
            position: absolute;
            top: 75%;
            width: 250px;
            left: 90px;


            span {
                margin-left: 10px;
                height: 30px;
                display: block;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        .ex {
            width: 40%;
            height: 55%;
            top: 30%;
            left: 40%;
            position: absolute;
            background-color: rgb(246, 249, 252);
            overflow-y: scroll;
        }

        .score {
            position: absolute;
            left: 15%;
            top: 15%;
        }

        .details {
            position: absolute;
            right: 5%;
            top: 30%;
        }

        .cancel {
            position: absolute;
            right: 5%;
            top: 65%;
        }
    }

}
</style>